.projects-background{
    background-color: var(--light-white);
    height: 100vh;
    width: 100vw;
    position: fixed;
    top: 0;
    left: 0;

    opacity: 0;
    animation-name: fade-in;
    animation-duration: 0.5s;
    animation-fill-mode: forwards;
}

.projects-page{
    position: absolute;
    top: 58px;
    border-radius: 14px;
    left: 0;
    z-index: 20;
    width: 100vw;
    height: fit-content;
    background-color: var(--light-white);

    /* padding-bottom: 10vw; */
    
    opacity: 0;
    animation-name: fade-up;
    animation-duration: 0.4s;
    animation-fill-mode: forwards;
    animation-delay: 0.2s;

    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.219);
}

.projects-title-position{
    /* background-color: red; */
    width: 100vw;
    height: 60vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.projects-title-container{
    /* background-color: blue; */
    width: 50%;

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
}

.projects-title{
    font-size: 9vw;
    margin-bottom: 10px;
}

.projects-subtitle{
    font-size: 0.9vw;
    line-height: 1.1;
}

.all-projects{
    height: fit-content;
    /* background-color: red; */
    display: flex;
    flex-direction: column;
    align-items: center;
}

.project-line{
    /* background-color: blue; */
    width: 95%;
    height: 70vh;
    margin: 10px 0;
    display: flex;
    justify-content: center;
    /* justify-content: center; */
}

.project{
    /* background-color: green; */
    height: 100%;
    width: 100%;
    margin: 0 10px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    color: var(--light-white);
    position: relative;
    border-radius: 15px;

    display: flex;
    flex-direction: column;
    align-items: center;

    /* border: 3px solid transparent; */

    transition: 0.3s;

    /* cursor: url("../assets/cursor2.png"), auto; */
}

.project:hover{
    transform: scale(1.01);
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.404);
    /* border: 3px solid var(--light-white); */
}

.project-location, .project-time{
    font-size: 1vw;
}

.project-quick-info{
    /* background-color: red; */
    display: flex;
    width: 90%;
    margin-top: 3vh;
    justify-content: space-between;
    text-align: right;
    align-items: center;
}

.project-title{
    font-size: 2.5vw;
    margin-bottom: 5px;
}

.project-description-container{
    opacity: 0;
    position: absolute;
    border-bottom-left-radius: 15px;
    border-bottom-right-radius: 15px;
    background: linear-gradient(to top, rgba(0, 0, 0, 0.932) 0%, rgba(0, 0, 0, 0) 100%);
    color: var(--light-white);
    bottom: 0;
    left: 0;
    padding: 6vh;
    transition: 0.3s;
}

.project:hover > .project-description-container{
    /* transform: translateY(-20px); */
    opacity: 1;
}

.project-description{
    transition: 0.3s;
    font-size: 1.1vw;
    text-transform: uppercase;
    line-height: 1.2;
    letter-spacing: 1px;
}

.project-description h2{
    margin-bottom: 20px;
    font-size: 2.4vw;
}

.project-description p{
    line-height: 1.2;
    letter-spacing: 1px;
    font-size: 1.1vw;
}

.project:hover .project-description{
    transform: translateY(-5px);
}

.project-set{
    display: flex;
    align-items: center;
    /* flex-direction: column; */
    /* background-color: red; */
}

.icon-container{
    /* background-color: blue; */
    display: flex;
    align-items: center;
}

.project-icon{
    margin-left: 15px;
    /* background-color: yellow; */
    width: 3vw;
    height: auto;
    color: var(--light-white);
}

#progress-icon{
    width: 1.5vw;
}

.project-links{
    margin-top: 35px;
}

.project-link{
    /* background-color: rgba(255, 255, 255, 0.397); */
    margin-right: 20px;
    padding: 12px 30px;

    color: var(--light-white);
    background-color: rgba(255, 255, 255, 0.192);
    /* border: 2px solid var(--light-white); */
    border-radius: 100px;
    font-size: 0.6vw;
    font-weight: normal;
    transition: 0.3s;
}

.project-link:hover{
    background-color: var(--light-white);
    color: black;
}

#project-1{
    background-image: url("../assets/karjat-india.jpg");
    width: 66.66%;
}

#project-2{
    background-image: url("../assets/umd-bitcamp.jpg");
    background-position: 100% 80%;
    width: 33.33%;
}

#project-3{
    background-image: url("../assets/umbc-hack.jpg");
}

#project-4{
    background-image: url("../assets/space-3.jpg");
    width: 33.33%;
}

#project-5{
    background-image: url("../assets/buzzword-5.jpg");
    width: 66.66%;
}

.projects-footer{
    width: 100vw;
    height: 40vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.next-page-button{
    background-color: black;
    color: var(--light-white);
    /* border: 1.5px solid black; */
    font-size: 5vw;
    font-family: "Six Caps";
    width: 50%;
    height: 50%;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.3s;
}

.next-page-button:hover{
    transform: scale(1.05);
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.404);
}

.next-page-page{
    background-color: var(--warm-black);
    z-index: 100;
    color: var(--warm-black);
    /* border: 1.5px solid black; */
    font-size: 5vw;
    font-family: "Six Caps";
    bottom: 0;
    left: 0;
    width: 100vw;
    height: 1000vh;
    /* border-radius: 10px; */
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.4s;

    /* animation-name: fadeIn;
    animation-duration: 0.5s;
    animation-fill-mode: forwards; */
}


@keyframes fade-up {
    0% {
        opacity: 0;
        transform: translateY(13vh);
    }
    100% {
        opacity: 100%;
        transform: translateY(0);
    }
}

@keyframes fade-in {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}