.photos-background{
    background-color: var(--warm-black);
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 101;
}

.photos-page{
    z-index: 102;
    background-color: var(--warm-black);
    position: absolute;
    top: 58px;
    border-top-left-radius: 14px;
    border-top-right-radius: 14px;
    left: 0;
    width: 100vw;
    height: 200vh;
    box-shadow: 0px -10px 15px rgba(0, 0, 0, 0.219);

    opacity: 0;
    animation-name: fade-up;
    animation-duration: 0.4s;
    animation-fill-mode: forwards;
    animation-delay: 0.2s;
}

#title-for-photo-page{
    color: var(--light-white);
}

@keyframes fade-up {
    0% {
        opacity: 0;
        transform: translateY(13vh);
    }
    100% {
        opacity: 100%;
        transform: translateY(0);
    }
}

@keyframes fade-in {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}