.landing-page{
    background-image: url('../assets/landing-bgs/maryland-plants.jpg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    width: 100vw;
    height: 100vh;
    color: var(--light-white);
}

.title-container{
    /* background-color: red; */
    width: 100%;
    height: 88.7vh;
    display: flex;
    /* justify-content: center; */
    align-items: center;
    padding-left: 6vw;

    position: absolute;
    bottom: 0;
    left: 0;
}

.my-name{
    font-size: 12vw;
    letter-spacing: 5px;
    line-height: 0.9;
    margin-bottom: 20px;
}

.sub-title{
    font-size: 1.2vw;
    line-height: 1.1;
    margin-left: 5px;
}