.nav-container{
    position: absolute;
    top: 0;
    z-index: 10000;
}

.navbar{
    /* background-color: var(--dark-gray); */
    color: var(--light-white);
    /* border-bottom: 1.5px solid var(--light-white); */

    display: flex;
    justify-content: space-between;

    padding: 9px 50px;
}

.navbar-dark{
    color: black;
    
    display: flex;
    justify-content: space-between;

    padding: 9px 50px;
}

.nav-logo, .contact-email{
    color: var(--light-white);
    font-size: 14px;
}

.nav-logo-dark, .contact-email-dark{
    color: black;
    font-size: 14px;
}

.nav-right{
    display: flex;
    align-items: center;
    /* background-color: red; */
}

.open-menu{
    width: 25px;
    height: auto;
    margin-left: 45px;
    transition: 0.2s;
}

.open-menu:hover{
    transform: skew(-35deg);
}

.menu-border{
    width: 100vw;
    height: 30px;
    border-radius: 21px;
    border-top: 1.5px solid var(--light-white);
}